<template>

  <nav class="navbar navbar-dark bg-dark">
    <div class="container-fluid">
      <span class="navbar-brand mb-0 h1">Dont4get</span>
    </div>
  </nav>


  <div class="container-fluid">
    <div class="row">

      <div class="col-3">
        <button class="form-control btn btn-primary m-1" @click="addRecord">–î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å</button>

        <input class="form-check-input m-1" type="checkbox" name="" id="showAll" v-model="viewAll"> <label
          for="showAll">–ø–æ–∫–∞–∑–∞—Ç—å –≤—Å–µ</label>
        <ul>
          <li v-for="record in db" @click="select(record.id)" :class="{ active: record.selected }" :key="record.id">
            {{ record.title }}
          </li>
        </ul>
      </div>

      <div class="col-9">
        <div v-for="record in db" :key="record.id">
          <div v-if="record.selected">
            <input class="form-control mt-2 mb-2" type="text" v-model="record.title" @keyup="changeField">
            <div class="row align-items-center">
              <div class="col-auto">
                <label class="col-form-label" for="">–Ø–∑—ã–∫</label>
              </div>
              <div class="col-auto">
                <input class="form-control mt-2 mb-2" type="text" v-model="record.lang" @keyup="changeLang">
              </div>
            </div>

            <code-editor :language_selector="false" :hide_header="true" :languages="[[record.lang]]" spellcheck="false"
              width="auto" v-model="record.body" :key="timer" @keyup="changeField"></code-editor>
            <div id="uuid">{{ record.id }}</div>
            <div>{{ record.date }}</div>
            <button class="btn-del btn btn-outline-danger mb-2 mt-2" @click="delRecord(record.id)">–£–¥–∞–ª–∏—Ç—å
              –∑–∞–ø–∏—Å—å</button>

          </div>
        </div>



        <div>
          <!-- <code><pre>{{ $data }}</pre></code> -->
        </div>
      </div>

    </div>
  </div>


</template>



<script>
import { v4 as uuidv4 } from 'uuid';
import db from "./db"
import CodeEditor from 'simple-code-editor';
export default {
  components: {
    CodeEditor
  },
  data() {
    return {
      timer: "",
      viewAll: false,
      db,
    }
  },
  methods: {
    select(id) {
      this.db.forEach(el => {
        el.selected = false;
      })
      this.viewAll = false;
      this.db[this.db.findIndex(el => el.id === id)].selected = true;
      this.changeField();
    },


    addRecord() {
      this.db.forEach(el => {
        el.selected = false;
      })
      this.db.push({
        id: uuidv4(),
        title: '–ù–æ–≤–∞—è –∑–∞–º–µ—Ç–∫–∞',
        body: '',
        selected: true,
        date: new Date(),
        lang: "javascript",
      });
      this.changeField();
    },


    delRecord(id) {
      this.db.splice(this.db.findIndex(el => el.id === id), 1);
      this.changeField();
    },


    changeLang() {
      this.timer = new Date().getTime();
      this.changeField();
    },


    changeField() {
      console.log('save to localStorage');
      localStorage.db = JSON.stringify(this.db);
    },


    showAllRecords() {
      this.db.forEach(el => {
        el.selected = true;
      });
      this.changeField();
    },

    hideAllRecords() {
      this.db.forEach(el => {
        el.selected = false;
      });
      this.changeField();
    }
  },

  mounted() {
    console.log('mounted');
    if (localStorage.db) {
      this.db = JSON.parse(localStorage.db);
    }
  },

  watch: {
    viewAll(newValue) {
      if (newValue == true) this.showAllRecords()
      else this.hideAllRecords();
    }
  }
}
</script>



