li {
  cursor: pointer;
  list-style-type: none;
  user-select: none;
}

li::before {
  content: "üìù ";
}

li:hover {
  color: rgb(13, 94, 13);
  font-weight: bold;
}

#uuid {
  font-size: xx-small;
  color: rgb(218, 218, 218);
}

li.active {
  font-weight: bold;
}

.btn-del {
  float: right;
}

li:nth-child(odd) {
  background-color: rgb(240, 240, 240);
}
